













import { Component, Vue } from 'vue-property-decorator';
// 定义接口*****************
interface Message {
  account: string;
  accountPlaceholder: string;
  password: string;
  passwordPlaceholder: string;
}
// 声明组件*****************
@Component
export default class BindJupiter extends Vue {
  // 属性*****************
  private bingData: Message = {
    account: '',
    accountPlaceholder: '请输入您的账号/手机号',
    password: '',
    passwordPlaceholder: '请输入您的密码',
  };
  private pageType: string = 'bind';

  // 挂载完成*****************
  mounted ():void {
    this.pageType = this.$route["query"]["pagetype"] == "unbundling" ? "unbundling" : "bind";
  }

  // 方法*****************
  // 提交绑定
  private bing(): void {
    console.log('---', this.bingData);
  }
  // 清空密码
  private emptyPassword(): void {
    this.bingData.password = '';
  }

}
